@font-face {
    font-family: 'SF Pro Display';
    font-display: swap;
    src:
        url('../fonts/SFProDisplay-Bold.woff2') format('woff2'),
        url('../fonts/SFProDisplay-Bold.woff') format('woff');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'SF Pro Display';
    font-display: swap;
    src:
        url('../fonts/SFProDisplay-Regular.woff2') format('woff2'),
        url('../fonts/SFProDisplay-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

@font-face {
  font-family: 'Space Age Cyrillic Regular';
  font-display: swap;
  src:
      url('../fonts/spaceagecyrillic-regular.woff2') format('woff2'),
      url('../fonts/sspaceagecyrillic-regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
